.wrapper {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid #e8e8ea;
  border-radius: 4px;
  margin-bottom: 10px;
}

.title {
  font-weight: 600;
  font-size: 24px;
  margin-bottom: 12px;
}

.info {
  display: flex;
  font-size: 16px;
  font-weight: 400;
  color: #4d4d4d;
}

.info-link {
  color: #4d4d4d;
  margin-right: 10px;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}
